.editor {
  width: 900px;
  height: 1200px;
  box-shadow: 0 0 2px black;
  margin: 50px auto;
  padding: 80px 50px;
  box-sizing: border-box;
}

span {
  font-size: 1.5rem;
  position: relative;
  word-wrap: break-word;
  margin: 0 0.5px;
}

.space {
  display: inline-block;
  height: 1.7rem;
  width: 0.5rem;
  margin: 0;
  transform: translateY(6px);
}

.text {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  display: inline-block;
  height: 100%;
  border-right: 0.5px solid black;
  animation-name: blink;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
  animation-duration: 0.2s;
  padding-right: 0.5px;
}

.enter {
  height: 1.5rem;
  font-size: 1.5rem;
  position: relative;
  width: 0;
}

button , input {
  margin: 1rem auto;
  display: block;
}

@keyframes blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
